.neon-button {
    font-size: 2rem;
    display: inline-block;
    padding: .25em .6em;
    margin-bottom: 1.5em;
    color: #FF44CC;
    color: var(--clr-pink);
    cursor: pointer;
    text-decoration: none;
    border: #FF44CC .15em solid;
    border: var(--clr-pink) .15em solid;
    position: relative;
    border-radius: .25em;

    text-shadow: 0 0 0.125em hsl(0 0% 100% / .2),
    0 0 0.45em #FF44CC;

    text-shadow: 0 0 0.125em hsl(0 0% 100% / .2),
    0 0 0.45em var(--clr-pink);

    box-shadow: inset 0 0 .69em 0 #FF44CC,
    0 0 1em 0 #FF44CC;

    box-shadow: inset 0 0 .69em 0 var(--clr-pink),
    0 0 1em 0 var(--clr-pink);

    transition: background-color 100ms linear;
}

.neon-button::before {
    content: "";    
    position: absolute;
    background: #FF44CC;
    background: var(--clr-pink);
    top: 120%;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;

    -webkit-transform: perspective(1em) rotateX(40deg) 
    scale(1, .35);

            transform: perspective(1em) rotateX(40deg) 
    scale(1, .35);
    -webkit-filter: blur(1em);
            filter: blur(1em);
    opacity: .7;

}

.neon-button:hover,
.neon-button:focus {
    color:orange;
    text-shadow: 0 0 0.125em hsl(0 0% 100% / .2),
    0 0 0.45em orange;
    border:orange .15em solid;
    box-shadow: inset 0 0 .69em 0 orange,
    0 0 1em 0 orange;
    box-shadow: inset 0 0 .69em 0 orange,
    0 0 1em 0 var(--clr-orange);
}

.neon-button a:hover {
    color: orange;
}
.neon-button:hover::before {
    background: orange;
}

