}

.modal-body__img {
    width: 200em;
}

.modal-body__img img{
    padding-right: 1em;
    object-fit: cover;
}

.position-left {
    object-position: left;
}

.overlay {
    position: fixed;
    opacity: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .6);
    pointer-events: none;
}

.overlay.active {
    pointer-events: all;
    opacity: 1;
}

